.login-container, .protected-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: sans-serif;
}

.login-title, .protected-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.login-button {
  padding: 10px 20px;
  font-size: 16px;
  margin: 10px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.google {
  background-color: #db4437;
  color: white;
}

.saml {
  background-color: #4285f4;
  color: white;
}

.protected-info {
  font-size: 18px;
  margin: 5px 0;
}

.protected-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-top: 10px;
}
